import React, { Component } from 'react'
import Img from "gatsby-image"

import Layout from "../components/layout"
import SEO from "../components/seo"

import '../styles/contact.scss'

export const query = graphql`
  query {
    craft {
      seomatic(uri: "/contact") {
        metaTitleContainer
        metaTagContainer
        metaLinkContainer
        metaScriptContainer
        metaJsonLdContainer
      }

      entries(section: [contact]) {
        ... on Craft_Contact {
          contactModules {
            ... on Craft_ContactModulesContacttitlemodule {
              contactOfficeImage {
                width
                height
                url
                ... on Craft_UploadsVolume {
                  url

                  imageFile {
                    childImageSharp {
                      fluid(maxWidth: 650) {
                        ...GatsbyImageSharpFluid_withWebp_noBase64
                      }
                    }
                  }

                  optimizedimages {
                    colorPalette
                  }
                }
              }
              contactTitle
              contactDetails{
                content
              }
              linkOneTitle
              linkOneUrl{
                content
              }
              linkTwoTitle
              linkTwoUrl{
                content
              }
            }
          }
        }
      }
    }
  }    
`;

export default class Contact extends Component {
  state = {
    activeImages: { 0: 0, 1: 0}
  }

  imageSequenceIntervals = [];
  timing = 5000;

  constructor(props){
    super();

    this.data = props.data.craft;
  }

  componentDidMount(){
    let columnCounts = [];

    this.data.entries[0].contactModules.forEach(column => {
      columnCounts.push( column.contactOfficeImage.length );
    })

    this.imageSequenceAnimate( columnCounts );
  }

  componentWillUnmount(){
    if(this.imageSequenceIntervals.length === 0){
      this.imageSequenceIntervals.forEach(interval => {
        clearInterval(interval);
      })
    }
  }

  imageSequenceAnimate = columnCounts => {
    columnCounts.forEach(( colCount, i ) => {
      setTimeout(() => {
        if(colCount > 1) this.imageSequenceIntervals.push( this.createSequenceInterval(colCount, i) )
      }, i * this.timing / 2) // to offset every even contact image
    })
  }

  createSequenceInterval = ( colCount, i ) => {
    return(
      setInterval(() => {
        let currActive = this.state.activeImages[i],
            newActive = (currActive === colCount - 1) ? 0 : currActive + 1;

        this.setState({
          activeImages: { ...this.state.activeImages, [i]: newActive }
        })
      }, this.timing)
    )
  }

  addTargetBlanks = string => {
    return string.replace(/href="http/g, 'target="_blank" href="http');
  }

  render(){
    const seomatic = this.data.seomatic,
          entries = this.data.entries[0];

    return (
      <Layout className="contact">
        <SEO data={ seomatic }/>
        <div className="contact-container">
          {entries.contactModules.map(( mod, i ) => {
            return(
              <div className="contact-column" key={ mod.contactTitle }>
                <div className="contact-imageSequence">
                  {mod.contactOfficeImage.map(( officeImg, j ) => {
                    let active = (this.state.activeImages[i] === j) ? '-active' : '';

                    return(
                      <Img 
                        key={`sequence ${i} image ${j}`}
                        className={`contact-imageSequence-image ${active}`}
                        style={{ position: "absolute" }}
                        fluid={ officeImg.imageFile.childImageSharp.fluid }
                        backgroundColor={ officeImg.optimizedimages.colorPalette[0] ? officeImg.optimizedimages.colorPalette[0] : "#efefef" }
                      />
                    )
                  })}
                </div>
                
                <div className="contact-text">
                  <h2 className="contact-city">{mod.contactTitle}</h2>
                  <div className="contact-details" dangerouslySetInnerHTML={{ __html: this.addTargetBlanks(mod.contactDetails.content) }}/>
                </div>

                <div className="contact-links">
                  {mod.linkOneTitle ? (
                    <div className="contact-links-row">
                      <p className="contact-links-type">{mod.linkOneTitle}</p>
                      <span dangerouslySetInnerHTML={{ __html: this.addTargetBlanks(mod.linkOneUrl.content) }}/>
                    </div>
                  ) : null}
                  
                  {mod.linkTwoTitle ? (
                    <div className="contact-links-row">
                      <p className="contact-links-type">{mod.linkTwoTitle}</p>
                      <span dangerouslySetInnerHTML={{ __html: this.addTargetBlanks(mod.linkTwoUrl.content) }}/>
                    </div>
                  ) : null}
                </div>
              </div>
            )
          })}
        </div>
      </Layout>
    ) 
  }
}